.heading{
  margin-top: 0px;
  background-color: black;
  border-radius: 30px;
  border: 10px;
  border-style: groove;
  color: white;
  text-align: center;
}

.inst:hover{
  background-color: black;
  color: white;
}

.start{
    background-color:black;
    height: 100px;
    width: 200px;
    border-radius: 30%;
    margin-top: 100px;
    float: right;
    margin-right: 20%;
    margin-left: 50px;
  }
  
  .start:hover{
    border-color:white;
    border-width: 10px;
  }
  
  .text{
    color: white;
  }
  
  .App-logo {
    height: 500px;
    width: 500px;
    pointer-events: none;
    margin-left: 30%;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
    }
  }
  
  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
